* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    color:rgba(255, 255, 255, 1);
}

body{
    /*background: #2608ec;*/
    
}

.wrapper{
    background: #048bc9;
    max-width: 423px;
    margin: 0 auto;
    border-radius: 40px;
}

.container{
    max-width: 343px;
    margin: 0 auto;
}

.city{
    padding-top: 44px;
    font-size: 18px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 12px;
}

.logo{
    width: 284px;
    height: 207px;
}

.title{
    font-size: 64px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 5px;
}

.onePart{
    padding-bottom: 31px;
}

.maxMin{
    display: flex;
    gap: 10px;
    font-size: 18px;
    font-weight: 400;
    justify-content: center;
}

.twoPart{
    background: rgba(1, 51, 120, 0.677);
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    padding: 7px 10px;
    margin-bottom: 20px;
}

.twoPart__cont {
    display: flex;
    align-items: center;
    gap: 5px;
}

.twoPart__one{
    padding-left: 11px;
}

.treePart{
    background: rgba(1, 51, 120, 0.677);
    border-radius: 20px;
}

.treePart__title{
    display: flex;
    justify-content: space-between;
    padding: 12px 18px 12px 16px;
}

.today{
    font-size: 20px;
    font-weight: 700;
}

.date{
    font-size: 18px;
    font-weight: 400;
}

.weathImg{
    width: 43px;
    height: 43px;
}

.cards{
    padding: 0 14px 14px 13px;
    display: flex;
    gap: 12px;
}

.card{
    padding: 13px 15px 20px 15px;
}

.card:hover{
    background: rgba(0, 68, 171, 1);
    border-radius: 20px;
    border: 1px solid rgba(80, 150, 255, 1);
}

.card__temp{
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 30px;
}

.time{
    font-size: 18px;
    font-weight: 400;
    padding-top: 7px;
}

.but{
    display: flex;
    justify-content: center;
}

.button{
    width: 142px;
    height: 47px;
    background: rgba(8, 36, 79, 1);
    border-radius: 20px;
    border: 0px;
    margin: 49px 0 56px 0;
    text-align: center;
}